.LandigPage_btn__ySGOZ {
	line-height: 50px;
	text-align: center;
	width: 250px;
    padding: 15px;
    font-size: 2em;
    font-weight: 300;
    letter-spacing: 3px;
    text-transform: uppercase;
	cursor: pointer;
    text-decoration: none;
    color: #ffffff;
    transition: all 0.5s;
	position: relative;
}

.LandigPage_btn__ySGOZ:hover {
    color: white;
}

.LandigPage_btn__ySGOZ::before {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1;
    /*
	background-color: rgba(255,255,255,0.1);
    */
    background-color: rgba(66, 66, 66, 0.1);
	transition: all 0.3s;
    
}
.LandigPage_btn__ySGOZ:hover::before {
	opacity: 0 ;
	transform: scale(0.5,0.5);
}
.LandigPage_btn__ySGOZ::after {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1;
	opacity: 0;
	transition: all 0.3s;
	border: 1px solid rgba(255,255,255,0.5);
	transform: scale(1.2,1.2);
}
.LandigPage_btn__ySGOZ:hover::after {
	opacity: 1;
	transform: scale(1,1);
}

